body {
  font-family: Arial, sans-serif;
  margin: 0;
  background: #f4f4f4;
}

.navbar {
  background: #333;
  color: #fff;
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

.navbar ul {
  list-style: none;
  display: flex;
  gap: 15px;
}

.navbar a {
  color: white;
  text-decoration: none;
}

.container {
  padding: 20px;
}
